@use "../mixable" as *;
@use "../global" as *;

/* overlay start */
.overlay {
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.98);
  height: 0%;
  left: 0;
  overflow-y: hidden;
  position: fixed;
  top: 0;
  transition: all 0.5s ease-in-out;
  width: 100%;
  z-index: 60;

  .close_btn {
    cursor: pointer;
    height: 36px;
    // opacity: 0.5;
    overflow: hidden;
    pointer-events: none;
    @extend %absolute;
    right: 1rem;
    text-indent: 100%;
    top: 1rem;
    transform: scale3d(0, 0, 1);
    width: 36px;

    &::before,
    &::after {
      // background-color: var(--secondary_color);
      background-color: #fff;
      border-radius: 3px;
      content: "";
      height: 100%;
      left: 50%;
      @extend %absolute;
      top: 0;
      width: 2px;
    }

    &:hover {
      opacity: 1;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  @media (max-width: 767px) {
    overflow-y: auto;
  }
}
/* overlay end */

/* overlay open start */
.open {
  height: 100%;

  .close_btn {
    pointer-events: auto;
    transform: scale3d(1, 1, 1);
    transition: opacity 0.3s, transform 0.3s;
    transition-delay: 0.5s;
  }
}

/* overlay open end */

/* header start */
header {
  // margin-top: 20px;
  @extend %relative;
  transition: transform 0.3s ease-in-out 0.2s;
  width: 100%;

  &.header_fixed {
    position: fixed;
    top: 0;
    z-index: 20;

    &.move_fixed {
      position: fixed;
      top: 0;
    }

    &.move {
      top: 0;
    }
  }

  .header_top {
    background-color: #d1d3d4;
    height: 33px;
    opacity: 0.21;
  }

  .topbar {
    background-color: var(--secondary_color);
    color: #fff;
    font-size: 0;

    ul {
      @include list_clear;

      li {
        display: inline-block;
      }
    }

    .top_btn_group {
      overflow: hidden;
      @extend %relative;

      a {
        color: #fff;
        display: inline-block;
        @include font-size(12px);
        margin-right: 1px;
        padding: 5px;
        text-decoration: none;

        @media (max-width: 575.98px) {
          padding: 10px 6px;
        }

        /* (320x480) iPhone (Original, 3G, 3GS) */
        @media only screen and (min-width: 320px) and (max-width: 480px) {
          /* insert styles here */
          padding: 10px 3px;
        }

        &.search_trigger {
          color: #fff;
        }
      }
    }

    .top_link_holder {
      float: right;

      li {
        padding: 10px 15px;
      }

      a {
        @include font-size(12px);

        &:focus,
        &:hover {
          text-decoration: none;
        }
      }
    }
  }

  .logo {
    display: inline-block;
    font-size: 0;
    @extend %relative;
    text-align: center;
    width: 450px;
    z-index: 3;

    /* 
    @media screen and (max-width: 1440px) {
      margin-top: 0;
      width: 140px;
    }

    @media screen and (max-width: 1366px) {
      margin-top: 0;
      width: 140px;
    } */

    @media (min-width: 992px) and (max-width: 1199.98px) {
      margin: 10px 0;
    }

    @media (min-width: 768px) and (max-width: 991.98px) {
      margin: 10px 0;
    }

    @media (min-width: 576px) and (max-width: 767.98px) {
      margin-top: 0;
      width: 120px;
    }

    @media (max-width: 575.98px) {
      margin-top: 0;
      width: 200px;
    }

    img {
      width: 100%;
    }
  }

  &.header_up {
    transform: translateY(-100%);

    @media (min-width: 992px) and (max-width: 1199.98px) {
      transform: none;
    }

    @media (min-width: 768px) and (max-width: 991.98px) {
      transform: none;
    }

    @media (min-width: 576px) and (max-width: 767.98px) {
      background-color: rgba(#000, 0.1);
      backdrop-filter: blur(50px) saturate(1.5);
      transform: none;
    }

    @media (max-width: 575.98px) {
      background-color: rgba(#000, 0.1);
      backdrop-filter: blur(50px) saturate(1.5);
      transform: none;
    }
  }

  &.header_down {
    transform: none;
  }

  &.active {
    background-color: #000;
  }
}

.header_row {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 10px 38px;
  @extend %relative;
  width: 100%;

  @media (max-width: 767.98px) {
    padding: 6px 26px;
  }

  .search_btn {
    align-items: center;
    display: inline-flex;
    height: 42px;
    justify-content: center;
    padding: 0;
    width: 42px;
  }
}

/* menu styling start */
.menu_container {
  color: #fff;
  font-size: 0;
  width: 100%;

  ul {
    @include list_clear;
  }
}
/* menu styling end */

/* lg screen menu start */
.lg_menu {
  display: none;
  transition: all 0.5s ease-in-out;

  .close_btn {
    display: none;
  }

  &.disappear {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }

  nav {
    ul {
      @include list_clear;
    }

    // This hover effect is cool, let's keep it.
    &:hover {
      > ul {
        > li {
          --_translate: 100%;
          --_scale-delay: 200ms;
        }
      }
    }

    > ul {
      display: flex;
      gap: 10px; // Add some gap between items
      justify-content: flex-start; // Use flex-start to align left
      // @extend %relative;

      > li {
        display: inline-block;
        @extend %relative;
        text-align: center;

        &.has-mega-menu {
          position: static;
        }

        // Top-level link styling
        > a {
          color: var(--brand_color_5);
          display: block;
          @include font-size(16px);
          font-weight: 500;
          padding: 25px 20px;
          @extend %relative;
          text-decoration: none;
          z-index: 1;

          // Media queries for font-size are fine
          @media screen and (max-width: 1440px) {
            @include font-size(13px);
            padding: 20px 14px;
          }

          @media screen and (max-width: 1366px) {
            @include font-size(13px);
            padding: 20px 14px;
          }

          @media screen and (max-width: 1280px) {
            @include font-size(12px);
            padding: 0;
          }

          // The background hover effect
          &::before {
            background-color: rgb(245, 245, 245);
            border-radius: 0.5rem;
            bottom: 14px;
            content: "";
            height: calc(100% - 28px);
            left: 0;
            pointer-events: none;
            @extend %absolute;
            right: 0;
            top: 14px;
            // Use the CSS variables controlled by the parent LI:hover
            scale: var(--_scale, 0) 1;
            translate: var(--_translate, 0);
            transition: scale 0.1s var(--_scale-delay, 0s), translate 0.2s;
            width: 100%;
            z-index: -1;
          }

          &:focus,
          &:hover {
            color: var(--primary_color);
            outline: none;
            text-decoration: none;
          }
        }

        // The main hover logic
        &:hover {
          --_scale: 1;
          --_translate: 0;
          --_scale-delay: 0ms;

          ~ li {
            --_translate: -10%;
            --_scale-delay: 100ms;
          }

          > a {
            color: var(--primary_color);
          }
        }

        // Dropdown icon for items with sub-menus
        &.has-sub > a,
        &.has-mega-menu > a {
          // MODIFIED: Apply to both types
          &::after {
            content: "\e604"; // simple-line-icons arrow
            display: inline-block;
            font-family: "simple-line-icons";
            @include font-size(9px);
            margin-left: 8px;
            transition: all 0.3s ease-in-out;
          }
        }

        &.no-title {
          .subnav_container {
            .subnav_single_holder {
              > a.mega-menu-title {
                display: none;
              }
            }
          }
        }

        // MODIFIED: Show dropdown arrow pointing up on hover
        &:hover > a::after {
          transform: rotate(180deg);
        }

        // --- SIMPLE DROPDOWN STYLING ---
        // MODIFIED: Target only simple dropdowns, not mega menus.
        > ul:not(.subnav_container) {
          background-color: #fff;
          border-radius: 5px;
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
          left: 0;
          list-style: none; // Ensure no bullets
          opacity: 0;
          padding: 10px 0;
          @extend %absolute;
          text-align: left;
          top: 90%;
          transition: all 0.3s ease-in-out;
          visibility: hidden;
          width: 220px;
          z-index: 10;

          > li {
            border-bottom: 1px solid var(--bs-border-color); // Separator lines
            padding: 0; // Reset padding on the li

            &:last-child {
              border-bottom: none; // No line on the last item
            }

            //  ***** THIS IS THE REFACTORED PART *****
            a {
              color: var(--body_text_color);
              display: block;
              @include font-size(14px);
              line-height: 1.5;
              padding: 10px 20px; // A bit more padding
              text-decoration: none;
              text-transform: none;
              transition: all 0.2s ease-out; // Smoother transition

              // The new hover effect - no <span> needed!
              &:focus,
              &:hover {
                // background-color: var(--primary_color);
                color: var(
                  --primary_color
                ); // Ensure text color stays white on hover
                transform: translateX(10px); // Slide link text to the right
              }
            }
          }
        }

        // Show simple dropdown on hover
        &:hover > ul:not(.subnav_container) {
          opacity: 1;
          top: 100%;
          visibility: visible;
        }

        // --- MEGA MENU STYLING ---
        // This is the container for the mega menu columns
        .subnav_container {
          background-color: #fff;
          border-radius: 0 0 0.75rem 0.75rem;
          box-shadow: 0 20px 36px rgba(0, 0, 0, 0.08);
          height: auto;
          left: 50%;
          opacity: 0;
          pointer-events: none;
          @extend %absolute;
          text-align: center;
          top: 120%;
          transform: translate(-50%, 20px);
          transition: all 0.3s ease-in-out;
          visibility: hidden;
          width: 94%;
          z-index: 10;

          // Wrapper to center the content
          .subnav_container_wrapper {
            align-items: flex-start; // Align columns to the top
            display: flex; // NEW: Use flexbox for columns
            gap: 30px; // NEW: Space between columns
            justify-content: center;
            margin: 0 auto;
            padding: 30px 20px;
            text-align: left;
            width: 80%;
          }

          // A single column in the mega menu
          .subnav_single_holder {
            flex: 1; // NEW: Allow columns to grow and shrink
            padding: 0;

            // Non-clickable title for a column
            > a.mega-menu-title {
              border-bottom: 1px solid #ddd;
              color: var(--primary_color);
              display: block;
              @include font-size(16px);
              font-weight: 600;
              margin-bottom: 15px;
              padding-bottom: 10px;
              pointer-events: none; // Make it non-clickable
              text-decoration: none;
              text-transform: uppercase;

              &::after {
                display: none; // Hide arrow on title
              }
            }

            // Image holder styles
            &_image {
              margin-bottom: 10px;
              img {
                border-radius: 5px;
                height: auto;
                width: 100%;
              }
            }
            &_title {
              color: var(--body_text_color);
              font-weight: bold;
            }

            // The list of links within a column
            ul {
              background: transparent;
              box-shadow: none;
              opacity: 1;
              position: static;
              transform: none;
              visibility: visible;
              width: 100%;

              li {
                a {
                  color: var(--body_text_color);
                  display: inline-block;
                  @include font-size(14px);
                  padding: 8px 0;
                  text-decoration: none;
                  text-transform: none;
                  transition: all 0.2s ease;

                  &:hover {
                    color: var(--primary_color);
                    transform: translateX(5px);
                  }
                }
              }
            }
          }
        }

        // MODIFIED: Show Mega Menu on hover
        &.has-mega-menu:hover > .subnav_container {
          opacity: 1;
          pointer-events: all;
          top: 84%;
          transform: translate(-50%, 0);
          visibility: visible;
        }
      }
    }
  }
}
/* lg screen menu end */

/* sm menu start */
.sm_menu {
  ul {
    font-size: 0;
    @include list_clear;
  }

  > ul {
    li {
      list-style-type: none;
      transition: all 0.5s ease-in-out;

      a {
        color: var(--body_text_color);
        display: flex;
        @include font-size(18px);
        font-weight: 500;
        justify-content: space-between;
        padding: 15px 12px;
        text-decoration: none;
      }

      ul {
        // display: none;

        li {
          padding-left: 6px;

          > a {
            background: none;
            @include font-size(14px);
            padding: 12px 18px;
            text-transform: none;
          }
        }
      }

      .subnav_container {
        .subnav_single_holder {
          &_title {
            @include font-size(18px);
            padding-left: 10px;
            pointer-events: none;
          }
        }
      }

      &.has_child_ul {
        > ul {
          display: none;
        }
      }

      &.has_child_div {
        .subnav_container {
          display: none;
        }
      }

      &.has_child_ul,
      &.has_child_div,
      &.has-sub {
        @extend %relative;

        > a {
          &::after {
            align-items: center;
            align-self: flex-end;
            content: "\ea13";
            display: inline-flex;
            font-family: "remixicon";
            @include font-size(18px);
            height: 20px;
            justify-content: center;
            justify-self: flex-end;
            @extend %relative;
            transition: all 0.3s ease-in-out;
            width: 20px;
          }
        }

        &.active {
          > a {
            &::after {
              content: "\f1af";
            }
          }
        }
      }
    }
  }
}
/* sm menu end */

.nav_holder {
  /* 
  background-color: #fff;
  box-shadow: 0 0 40px 10px rgb(0 0 0 / 10%); 
  */
  font-size: 0;
}

.menu_btn_holder {
  display: none;
  @extend %relative;
  width: 100px;
  z-index: 3;

  @media (min-width: 768px) and (max-width: 991.98px) {
    display: inline-block;
  }

  @media (max-width: 1199.98px) {
    display: inline-block;
    width: 60px;
  }

  @media (min-width: 576px) and (max-width: 767.98px) {
    margin: 10px 10px 10px 0;
    text-align: center;
    width: 40px;
  }

  @media (max-width: 575.98px) {
    margin: 10px 0;
    text-align: center;
    width: 40px;
  }

  a {
    align-items: center;
    color: var(--secondary_color);
    display: flex;
    height: 46px;
    justify-content: center;
    text-decoration: none;
    transition: all 0.5s ease-in-out;

    span {
      display: block;
      margin-right: 10px;
      @extend %relative;
      transition: all 0.3s ease-in-out 0s;
      width: 100%;

      @media (min-width: 576px) and (max-width: 767.98px) {
        margin-right: 0;
      }

      @media (max-width: 575.98px) {
        margin-right: 0;
      }

      small {
        background-color: var(--secondary_color);
        display: block;
        height: 2px;
        transition: all 0.3s ease-in-out 0s;
      }

      &::after,
      &::before {
        background-color: var(--secondary_color);
        content: "";
        height: 2px;
        left: 0;
        @extend %absolute;
        transition: all 0.3s ease-in-out 0s;
        width: 100%;
      }

      &::after {
        left: auto;
        right: 0;
        top: 10px;
        width: 70%;
      }

      &::before {
        top: -10px;
      }
    }

    .text {
      display: inline-block;
      @include font-size(14px);
      transition: all 0.3s ease-in-out 0s;

      @media (min-width: 576px) and (max-width: 767.98px) {
        display: none;
      }

      @media (max-width: 575.98px) {
        display: none;
      }
    }

    @media (hover: hover) and (pointer: fine) {
      &:focus,
      &:hover {
        .text {
          color: var(--primary_color);
        }

        span {
          small {
            background-color: var(--primary_color);
          }

          &::after,
          &::before {
            background-color: var(--primary_color);
          }
        }
      }
    }

    &.active {
      span {
        small {
          opacity: 0;
          transform: scale(0);
        }

        &::after,
        &::before {
          top: 0;
        }

        &::after {
          transform: rotate(45deg);
          width: 100%;
        }

        &::before {
          transform: rotate(-45deg);
        }
      }
    }

    &.rotate {
      transform: rotate(-90deg);
    }
  }
}

/* header end */

.offset_menu_holder {
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  height: 340px;
  left: 0;
  padding-top: 80px;
  @extend %absolute;
  top: 0;
  text-align: center;
  transform: translateY(-100%);
  transition: all 0.9s ease 0.2s;
  width: 100%;
  z-index: 1;

  &.in_view {
    transform: none;
  }

  a {
    color: #fff;
  }
}

/* search start */
.search_holder {
  align-items: center;
  background-color: #f6f6f6;
  display: flex;
  font-size: 0;
  gap: 10px;
  height: 100%;
  justify-content: space-between;
  opacity: 0;
  padding: 0 20px;
  pointer-events: none;
  @extend %absolute;
  top: -100%;
  transition: all 0.5s ease-in-out;
  width: 100%;
  z-index: 22;

  .close_search {
    align-items: center;
    background-color: var(--primary_color);
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    @include font-size(20px);
    height: 42px;
    justify-content: center;
    padding: 0;
    width: 42px;
  }

  .search_content {
    display: inline-flex;
    width: 95%;

    @media (max-width: 767.98px) {
      width: 82%;
    }

    .form-control,
    input[type="text"] {
      border-color: transparent;

      &:focus {
        border-color: var(--gray_c, #ccc);
        box-shadow: none;
        outline: none;
      }
    }
  }

  form {
    width: 100%;

    button[type="submit"] {
      background-color: transparent;
      border: 0 none;
      display: inline-block;
      height: 26px;
      padding: 0;
      text-align: center;
      width: 26px;
    }

    input[type="text"] {
      border-radius: 30px;
      padding: 11px 20px;
    }
  }

  &.open {
    opacity: 1;
    pointer-events: all;
    top: 0;

    .close_search {
      pointer-events: auto;
    }
  }
}

/* offcanvas menu */
.sm_offcanvas_wrapper {
  display: flex;
  height: 100vh;
  justify-content: flex-end;
  position: fixed;
  pointer-events: none;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 20;

  .offcanvas_overlay {
    background-color: rgba(#000, 0.8);
    height: 100%;
    left: 0;
    opacity: 0;
    pointer-events: none;
    @extend %absolute;
    top: 0;
    transition: all 0.5s;
    width: 100%;
    z-index: 1;
  }

  .sm_offcanvas_content {
    transform: translateX(100%);
    transition: transform 0.3s;
    z-index: 2;
  }

  .nav-wrapper {
    height: 100vh;
    @extend %relative;
    transform: translateX(100%);
    transition: transform 0.3s;
    width: 300px;

    &.show-menu {
      transform: none;

      .js-nav-toggle {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  .sm_offcanvas_extended {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    left: -60px;
    padding: 60px 0 30px;
    @extend %absolute;
    text-align: center;
    top: 0;
    width: 60px;

    ul {
      font-size: 0;
      @include list_clear;

      li {
        opacity: 0;
        transform: translateX(30px);
        transition: all 0.3s;

        &:not(:last-child) {
          margin-bottom: 10px;
        }

        a {
          color: #fff;
          @include font-size(16px);
          text-decoration: none;

          img {
            display: inline-block;
            width: 40%;
          }
        }
      }
    }
  }

  .logo {
    border-bottom: 0 none;
    display: inline-block;
    font-size: 0;
    left: 0;
    margin-top: 20px;
    padding: 0 10px;
    @extend %absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }

  .js-nav-toggle {
    color: #fff;
    display: block;
    float: left;
    height: 40px;
    left: -60px;
    margin: 15px 0 0 10px;
    opacity: 0;
    padding: 0;
    pointer-events: none;
    @extend %absolute;
    top: 0;
    transition: all 0.5s;
    width: 43px;
    z-index: 2;
    // border: 2px solid #345;

    span {
      background-color: transparent;
      display: block;
      height: 2px;
      margin: 17px auto 0;
      @extend %relative;
      transition: all 0.4s;
      transition-delay: 0.3s;
      width: 22px;

      &::before,
      &::after {
        box-shadow: 0 0 0 1px #fff;
        content: "";
        display: block;
        height: 0;
        left: 1px;
        margin-top: 0;
        @extend %absolute;
        top: 50%;
        transition: all 0.3s 0.3s;
        width: 20px;
      }

      &::before {
        transform: rotate(-45deg);
      }

      &::after {
        transform: rotate(45deg);
      }
    }
  }

  nav {
    background-color: #fafafa;
    box-shadow: 0 0 5px 1px #ddd;
    height: 100%;
    left: 0;
    overflow-x: hidden;
    overflow-y: auto;
    @extend %absolute;
    top: 0;
    width: 100%;

    ul {
      font-size: 0;
      @include list_clear;
    }

    .nav-toggle {
      background-color: #ededed;
      color: var(--primary_color);
      cursor: pointer;
      left: 0;
      padding: 0.45em 0.6em;
      @extend %absolute;
      top: 70px;
      transition: backgroun-color 0.2s;
      width: 100%;
      z-index: 100;

      &:hover {
        background-color: #e5e3e3;
      }

      &.back-visible {
        .nav-back {
          opacity: 1;
        }

        .nav-title {
          padding-left: 0;
          transform: translateX(40px);
        }
      }
    }

    .nav-title {
      @include font-size(13px);
      left: 0;
      padding-left: 0.7em;
      @extend %absolute;
      top: 14px;
      transition: transform 0.3s;
      text-transform: uppercase;
    }

    .nav-back {
      display: inline-block;
      height: 30px;
      opacity: 0;
      @extend %relative;
      transition: opacity 0.2s;
      text-align: center;
      vertical-align: middle;
      width: 30px;
      z-index: 1;

      &::before {
        content: "\EA64";
        font-family: "remixicon";
        @include font-size(18px);
        left: 50%;
        @extend %absolute;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }

    a {
      border-bottom: 1px solid #eee;
      color: #111;
      display: block;
      @include font-size(15px);
      padding: 0.7em;
      @extend %relative;
      text-decoration: none;
      transition: color 0.15s, background-color 0.15s;

      &:hover {
        background-color: #efefef;
        color: #333;
      }
    }

    ul {
      background-color: #fafafa;
      height: 100%;
      left: 0;
      list-style: none;
      padding: 114px 0 0;
      @extend %absolute;
      top: 0;
      transition: transform 0.3s;
      width: 100%;

      ul {
        display: none;
        left: 100%;
      }
    }

    li {
      &.has-dropdown {
        > a {
          overflow: hidden;
          padding-right: 2.5em;
          text-overflow: ellipsis;
          white-space: nowrap;

          &::after {
            border: 1px solid currentcolor;
            border-left-color: transparent;
            border-top-color: transparent;
            content: "";
            height: 9px;
            @extend %absolute;
            right: 1em;
            top: 50%;
            transform: translateY(-90%) rotateZ(-45deg);
            transform-origin: 100%;
            transition: transform 0.3s;
            width: 9px;
          }
        }
      }

      &.nav-dropdown-open {
        ul {
          display: block;
        }
      }

      ul {
        li {
          .subnav_single_holder_title {
            &.active {
              h4 {
                &::after {
                  transform: rotate(45deg);
                }
              }
            }
          }

          h4 {
            border-bottom: 1px solid #eee;
            color: #111;
            @include font-size(13px);
            line-height: normal;
            margin-bottom: 0;
            padding: 16px 10px 10px;
            @extend %relative;
            text-transform: uppercase;

            &::after {
              align-items: center;
              content: "\EA13";
              display: inline-flex;
              font-family: "remixicon";
              height: 18px;
              justify-content: center;
              @extend %absolute;
              right: 10px;
              top: 13px;
              transition: all 0.3s;
              width: 18px;
            }
          }

          .subnav_single_holder {
            ul {
              display: none;
            }
          }

          ul {
            left: 0;
            padding-top: 0;
            @extend %relative;
          }
        }
      }
    }
  }

  &.active {
    pointer-events: all;

    .offcanvas_overlay {
      opacity: 1;
      pointer-events: all;
      transition-delay: 0.2s;
    }

    .sm_offcanvas_content {
      transform: none;
    }

    .sm_offcanvas_extended {
      ul {
        li {
          opacity: 1;
          transform: none;
        }
      }
    }
  }
}

/* content overlay start */
.content_overlay_wrapper {
  border-radius: 0 0 0.78rem 0.78rem;
  display: flex;
  height: 0;
  justify-content: flex-start;
  left: 50%;
  margin-top: 84px;
  opacity: 0;
  overflow: hidden;
  pointer-events: none;
  position: fixed;
  top: 0;
  transform: translateX(-50%);
  transition: all 0.7s ease;
  visibility: hidden;
  width: 78%;
  z-index: 20;

  @media (min-width: 992px) and (max-width: 1199.98px) {
    margin-top: 120px;
  }

  @media (min-width: 768px) and (max-width: 991.98px) {
    margin-top: 132px;
  }

  .position_right {
    justify-content: flex-end;
  }

  .offcanvas_overlay {
    background-color: rgba(#000, 0.8);
    height: 100%;
    left: 0;
    opacity: 0;
    @extend %absolute;
    pointer-events: none;
    top: 0;
    transition: all 0.5s;
    width: 100%;
    z-index: 1;
  }

  .offcanvas_content {
    transition: transform 0.3s;
    // transform: translateX(-110%);
    width: 100%;
    z-index: 2;
  }

  .overlay_content_holder {
    height: 100%;
    @extend %relative;
    transition: transform 0.3s;
    width: 100%;
    // height: 100vh;
    // width: 100vw;

    &.show-menu {
      transform: none;

      .overlay_toggle {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  .overlay_toggle {
    color: #fff;
    display: block;
    float: left;
    height: 40px;
    opacity: 0;
    padding: 0;
    pointer-events: none;
    @extend %absolute;
    right: -44px;
    top: 0;
    transition: all 0.5s;
    width: 43px;
    z-index: 2;
    // border: 2px solid #345;
    // margin: 15px 0 0 10px;

    span {
      background-color: transparent;
      height: 2px;
      display: block;
      margin: 17px auto 0;
      @extend %relative;
      transition: all 0.4s;
      transition-delay: 0.3s;
      width: 22px;

      &::before,
      &::after {
        box-shadow: 0 0 0 1px #fff;
        content: "";
        display: block;
        height: 0;
        left: 1px;
        margin-top: 0;
        @extend %absolute;
        top: 50%;
        transition: all 0.3s 0.3s;
        width: 20px;
      }

      &::before {
        transform: rotate(-45deg);
      }

      &::after {
        transform: rotate(45deg);
      }
    }
  }

  .overlay_content {
    background-color: #fff;
    height: 100%;
    left: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 12px;
    @extend %absolute;
    top: 0;
    width: 100%;

    &.has_footer {
      padding-bottom: 70px;
    }

    &_footer {
      align-items: center;
      background-color: inherit;
      border-top: 1px solid var(--gray_d7, #d7d7d7);
      bottom: 0;
      display: inline-flex;
      justify-content: center;
      left: 0;
      padding: 10px 0;
      position: fixed;
      text-align: center;
      width: 300px;

      .btn {
        border-radius: 20px;
        @include font-size(14px);
        padding: 4px 26px;

        &:not(:last-child) {
          margin-right: 10px;
        }
      }
    }
  }

  &.active {
    height: 50vh;
    opacity: 1;
    pointer-events: all;
    visibility: visible;

    .offcanvas_overlay {
      opacity: 1;
      pointer-events: all;
      transition-delay: 0.2s;
    }

    .offcanvas_content {
      transform: none;
    }
  }
}
