@use "mixable" as *;

:root {
  --brand_color_1: #379934;
  --brand_color_1-rgb: 55, 153, 52;
  --brand_color_2: #212529;
  --brand_color_3: #7a7a7a;
  --brand_color_4: #1a2020;
  --brand_color_5: #002147;

  --selection_color: var(--brand_color_1);
  --primary_color: var(--brand_color_1);
  --secondary_color: var(--brand_color_2);
  --body_text_color: var(--brand_color_2);

  --round_full: 9999px;

  --body_font: "Lexend", sans-serif;

  --gray_color: #585858;
  --soft_border: #e9e9ef;
  --space_10: 10px;
  --space_20: 20px;
}

html {
  font-size: 100%;
}

::-moz-selection {
  background: var(--selection_color);
  color: #fff;
  text-shadow: none;
}

::-webkit-selection {
  background: var(--selection_color);
  color: #fff;
  text-shadow: none;
}

::selection {
  background: var(--selection_color);
  color: #fff;
  text-shadow: none;
}

/* scrollbar width */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* scrollbar track */
::-webkit-scrollbar-track {
  background-color: #f1f1f1;

  &:hover {
    background-color: #d6d6d6;
  }
}

/* scrollbar handle */
::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 20px;

  /* scrollbar handle on hover */
  &:hover {
    background-color: #555;
  }
}

body {
  color: var(--body_text_color);
  font-family: var(--body_font);
  @include font-size(16px);
  font-style: normal;
  font-weight: 400;

  /* 
  @media (min-width: 1024px) {
    @include font-size(18px);
    line-height: 1.75rem;
  }
     */
}
html.lenis,
html.lenis body {
  height: auto;
}
.lenis:not(.lenis-autoToggle).lenis-stopped {
  overflow: clip;
}
.lenis [data-lenis-prevent],
.lenis [data-lenis-prevent-wheel],
.lenis [data-lenis-prevent-touch] {
  overscroll-behavior: contain;
}
.lenis.lenis-smooth iframe {
  pointer-events: none;
}
.lenis.lenis-autoToggle {
  transition-property: overflow;
  transition-duration: 1ms;
  transition-behavior: allow-discrete;
}

hr {
  border: 0;
  border-top: 1px solid #ccc;
  display: block;
  height: 1px;
  margin: 1em 0;
  padding: 0;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

input[type="submit"],
button {
  cursor: pointer;

  &:focus {
    box-shadow: none;
    outline: none;
  }
}

a {
  transition: all 0.3s ease-in-out 0s;
  outline: none;

  &:focus {
    outline: none;
  }
}

video,
img {
  object-fit: cover;
}

table {
  th,
  td {
    @include font-size(13px);
  }
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-weight: 700;
}

b,
strong {
  font-weight: 700;
}

main {
  float: left;
  width: 100%;
}

%absolute {
  position: absolute;
}

%relative {
  position: relative;
}

%inline-block {
  display: inline-block;
}

.round_full {
  border-radius: var(--round_full);
}

.round_2xl {
  border-radius: 32px;
}

.round_xl {
  border-radius: 0.75rem;
}

.card_round {
  border-radius: 42px;
}

.block {
  display: block;
}

.clear {
  clear: both;
}

.flow {
  overflow: hidden;
}

.fixed {
  position: fixed;
}

.height {
  height: auto;
}

.height_flow {
  height: auto;
  overflow: hidden;
}

.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.sr-only.focusable:active,
.sr-only.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  white-space: inherit;
  width: auto;
}

.text-h3 {
  font-size: 2.25rem;
  line-height: calc(44 / 36);
  font-weight: 600;
}

.text-h5 {
  font-size: 1.5rem;
  line-height: calc(30 / 24);
  font-weight: 600;
}

.invisible {
  visibility: hidden;
}

.list_clear {
  @include list_clear;
}

.main_holder {
  @extend %relative;
  transition: all 0.3s ease;
  width: 100%;

  &.top_content_space {
    padding-top: 87px;

    @media (min-width: 992px) and (max-width: 1199.98px) {
      padding-top: 50px;
    }

    @media (min-width: 768px) and (max-width: 991.98px) {
      padding-top: 50px;
    }

    @media (min-width: 576px) and (max-width: 767.98px) {
      padding-top: 66px;
    }

    @media (max-width: 575.98px) {
      padding-top: 66px;
    }
  }
}

.container_content {
  margin: auto;
  max-width: 1920px;
  @extend %relative;
  width: 100%;

  @media (max-width: 767.98px) {
    padding: 0 12px;
  }
}

.shadow_simple {
  box-shadow: 0 0 5px #0003;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  @extend .flow;
  padding: 0;
  @extend %absolute;
  width: 1px;

  &.focusable:active,
  &.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
  }
}

.bg_f4 {
  background-color: #f4f4f4;
}

.bg_fb {
  background-color: #fbf9f9;
}

.bg_gray {
  background-color: #f1f1f2;
}

.bg_primary {
  background-color: var(--primary_color);
}

.primary_color {
  color: var(--primary_color);
}

.secondary_color {
  color: var(--secondary_color);
}

.gray_7 {
  color: var(--brand_color_3);
}

.bg_gray_7 {
  background-color: var(--brand_color_3);
}

.bg_light_logo {
  background-color: #f2f8f1;
}

.divider_heading {
  @extend %relative;
  margin-bottom: 2.5rem;
  padding-bottom: 2.5rem;

  &::before {
    background-color: var(--primary_color);
    bottom: 5px;
    content: "";
    display: block;
    height: 4px;
    @extend %absolute;
    width: 160px;
  }

  &.center {
    text-align: center;

    &::before {
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

.no_round {
  border-radius: 0;
}

.space_top {
  margin-top: 5px;
}

.top_space {
  @extend %relative;
  top: -70px;
  z-index: 5;

  @media (max-width: 575.98px) {
    top: -35px;
  }
}

.btn_fill {
  background-color: var(--primary_color);
  border: 1px solid var(--primary_color);
  color: #fff;
  @include font-size(16px);
  font-weight: 500;
  padding: 8px 32px;
  text-decoration: none;

  &:focus,
  &:hover {
    background-color: var(--secondary_color);
    border-color: var(--secondary_color);
    color: #fff;
  }

  &.light {
    background-color: #fff;
    border-color: #fff;
    color: var(--primary_color);

    &:focus,
    &:hover {
      background-color: var(--secondary_color);
      border-color: var(--secondary_color);
      color: #fff;
    }
  }
}

.btn_outline {
  border: 1px solid var(--secondary_color);
  border-radius: var(--round_full);
  color: var(--primary_color);
  @include font-size(16px);
  font-weight: 500;
  padding: 8px 32px;
  transition: all 0.3s ease-in-out;
  text-decoration: none;

  span {
    display: inline-block;
    width: 20px;

    svg {
      width: 100%;
    }
  }

  &:focus,
  &:hover {
    background-color: var(--secondary_color);
    border-color: var(--secondary_color);
    color: #fff;
  }

  &.light {
    border-color: #fff;
    color: #fff;

    &:focus,
    &:hover {
      background-color: #fff;
      border-color: #fff;
      color: #000;
    }
  }
}

.bg_f {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
}

.no_repeat {
  background-repeat: no-repeat;
}

.s_height {
  min-height: 100svh;
}

.negative {
  position: relative;

  @media screen and (min-width: 1366px) {
    // bottom: -119px;
    top: -119px;
  }

  @media screen and (min-width: 1440px) {
    top: -70px;
  }

  @media (max-width: 575.98px) {
    top: -35px;
  }
}

.nav_space {
  padding-top: 0;
}

.mh_46 {
  min-height: 46vw;

  @media screen and (min-width: 1921px) {
    min-height: 30vw;
  }

  @media (max-width: 767.98px) {
    min-height: 70vh;
  }
}

.fw_thin {
  font-weight: 100;
}

.fw_extra_light {
  font-weight: 200;
}

.fw_light {
  font-weight: 300;
}

.fw_md {
  font-weight: 500;
}

.fw_sbold {
  font-weight: 600;
}

.flip {
  transform: scale(-1);
}

.swiper_arrow_nav {
  @extend %relative;
  z-index: 2;

  .swiper-wrapper {
    padding-bottom: 6rem;
  }

  .swiper-pagination {
    .swiper-pagination-bullet {
      align-items: center;
      background-color: #fff;
      border-radius: 99px;
      color: var(--primary_color);
      display: inline-flex;
      height: 32px;
      justify-content: center;
      opacity: 1;
      margin: 0 4px;
      width: 56px;

      @media (max-width: 767.98px) {
        width: 46px;
      }
    }

    .swiper-pagination-bullet-active {
      background-color: var(--primary_color);
      color: #fff;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    align-items: center;
    background-color: #fff;
    border-radius: var(--round_full);
    bottom: 8px;
    color: var(--primary_color);
    display: inline-flex;
    @include font-size(28px);
    height: 32px;
    justify-content: center;
    top: auto;
    width: 56px;

    @media (max-width: 767.98px) {
      width: 46px;
    }

    .swiper-navigation-icon {
      display: none;
    }

    svg,
    img {
      display: inline-block;
      width: 80%;
    }

    &::after {
      display: none;
    }
  }

  .swiper-button-next {
    right: 27.2vw;

    @media screen and (min-width: 1550px) {
      right: 29.5vw;
    }

    @media screen and (min-width: 1800px) {
      right: 31.5vw;
    }

    @media screen and (min-width: 1921px) {
      right: 23.5vw;
    }

    @media (max-width: 767.98px) {
      right: 10px;
    }
  }

  .swiper-button-prev {
    left: 27.2vw;

    @media screen and (min-width: 1550px) {
      left: 29.5vw;
    }

    @media screen and (min-width: 1800px) {
      left: 31.5vw;
    }

    @media screen and (min-width: 1921px) {
      left: 23.5vw;
    }

    @media (max-width: 767.98px) {
      left: 10px;
    }
  }
}

@media print,
  (-webkit-min-device-pixel-ratio: 1.25),
  (min-resolution: 1.25dppx),
  (min-resolution: 120dpi) {
  /* Style adjustments for high resolution devices */
}

@media print {
  *,
  *::before,
  *::after {
    background: #fff !important;
    color: #000 !important;
    /* Black prints faster */
    box-shadow: none !important;
    text-shadow: none !important;
  }

  a,
  a:visited {
    text-decoration: underline;
  }

  a[href]::after {
    content: " (" attr(href) ")";
  }

  abbr[title]::after {
    content: " (" attr(title) ")";
  }

  a[href^="#"]::after,
  a[href^="javascript:"]::after {
    content: "";
  }

  pre {
    white-space: pre-wrap !important;
  }

  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  thead {
    display: table-header-group;
  }

  tr,
  img {
    page-break-inside: avoid;
  }

  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }

  h2,
  h3 {
    page-break-after: avoid;
  }
}

/* elements & global classes styles end */
