.btn_globe {
  align-items: center;
  color: #fff;
  display: grid;
  justify-content: center;
  text-decoration: none;

  > * {
    grid-column: 1;
    grid-row: 1;
  }

  span {
    background-color: var(--primary_color);
    border-radius: 999px;
    display: inline-block;
    justify-self: center;
    opacity: 0;
    @extend %relative;
    padding: 10px 20px;
    transition: opacity 0.9s ease;
    width: auto;
    z-index: 2;

    @media (max-width: 767.98px) {
      opacity: 1;
    }
  }

  img {
    @extend %relative;
    transition: opacity 0.3s ease;
    z-index: 1;
  }

  @media (hover: hover) and (pointer: fine) {
    &:hover {
      img {
        opacity: 0.2;
      }
      span {
        opacity: 1;
      }
    }
  }
}

.cable_network_section {
  @include smSpace;
}

.solution_section {
  @extend .cable_network_section;
}

.solution_intro {
  h1,
  h2 {
    font-weight: 600;
    margin-bottom: 30px;
  }

  p {
    margin-bottom: 20px;

    &:first-of-type {
      font-weight: 400;
      line-height: 1.5;
      margin-bottom: 30px;
    }
  }
}

.grid_wrapper {
  @media (max-width: 767.98px) {
    padding: 0 12px;
  }
}

.icon_box_single {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  height: 100%;
  @extend %relative;

  @media (max-width: 767.98px) {
    padding: 20px;
  }

  &_header {
    align-items: center;
    display: flex;
    gap: 20px;
    margin-bottom: 2px;

    @media screen and (min-width: 1199px) {
      min-height: 41%;
    }

    @media screen and (min-width: 1921px) {
      min-height: 54%;
    }

    svg {
      fill: var(--primary_color);
    }

    &_icon {
      width: 84px;

      @media screen and (max-width: 1440px) {
        width: 60px;
      }
      @media screen and (max-width: 1366px) {
        width: 60px;
      }

      @media screen and (min-width: 1921px) {
        width: 84px;
      }

      img,
      svg {
        height: auto;
        width: 100%;

        @media screen and (max-width: 1440px) {
          object-fit: contain;
          width: 58px;
        }
        @media screen and (max-width: 1366px) {
          object-fit: contain;
          width: 58px;
        }
      }
    }

    &_title {
      @include font-size(24px);
      line-height: 1.1;

      @media (max-width: 767.98px) {
        @include font-size(22px);
      }
    }
  }

  &_body {
    p {
      @include font-size(16px);
      line-height: 1.2;
    }
  }

  &_footer {
    align-items: center;
    display: flex;
    justify-content: flex-start;
    margin-top: 20px;
    padding: 0 20px;
    width: 100%;
  }

  &.bordered &_footer {
    padding: 0;
  }

  &.bordered {
    border: 2px solid var(--primary_color);
    min-height: 20rem;
    padding: 20px;
    justify-content: space-between;
  }

  &.solution {
    background-color: rgb(#fff, 0.5);
    border: 1px solid var(--primary_color);
    border-radius: 20px;
    color: var(--body_text_color);
    // margin-bottom: 30px;
    // min-height: 21rem;
    padding: 20px;

    @media screen and (min-width: 1600px) {
      min-height: 17rem;
    }

    @media screen and (max-width: 1440px) {
      min-height: 18rem;
      padding-top: 0;
    }

    @media screen and (max-width: 1366px) {
      min-height: 19rem;
      padding-top: 0;
    }

    @media screen and (max-width: 1280px) {
      min-height: 24rem;
    }

    @media (max-width: 767.98px) {
      min-height: 20rem;
    }

    .icon_box_single_header_title,
    .icon_box_single_body {
      color: var(--body_text_color);
    }

    .icon_box_single_header {
      flex-flow: row-reverse;
      justify-content: space-between;

      @media (min-width: 991.98px) {
        height: 110px;
      }

      @media (max-width: 767.98px) {
        margin-bottom: 20px;
        padding-top: 20px;
      }

      svg {
        fill: var(--body_text_color);
      }
    }

    .icon_box_single_header_icon {
      display: inline-flex;
      height: 100%;

      @media (max-width: 767.98px) {
        height: 80px;
        width: 20%;
      }
    }

    .icon_box_single_footer {
      bottom: 16px;
      left: 0;
      @extend %absolute;
    }
  }
}

.about_network_section {
  .icon_box_single {
    padding: 0 10px 20px;

    @media screen and (min-width: 1600px) {
      margin: 0 30px;
    }

    @media (max-width: 767.98px) {
      height: auto;
      margin-bottom: 12px;
      padding-bottom: 42px;
    }

    &_header {
      align-items: flex-start;
      min-height: 40%;

      @media (max-width: 767.98px) {
        margin-bottom: 0;
        min-height: auto;
      }

      &_title {
        margin-bottom: 20px;
        padding-left: 0;
      }
    }

    &_body {
      min-height: 8rem;

      @media (max-width: 767.98px) {
        min-height: auto;
      }

      p {
        margin-bottom: 0;
      }
    }

    &.network {
      min-height: auto;
    }
  }
}

.ent_solution_section {
  color: #fff;
  @include smSpace;

  .icon_box_single {
    background-color: transparent;
    border-color: #fff;

    &.solution {
      @media screen and (max-width: 1440px) {
        min-height: 20rem;
      }

      @media screen and (max-width: 1366px) {
        min-height: 20rem;
      }

      @media screen and (max-width: 1280px) {
        min-height: 21rem;
      }

      @media (max-width: 767.98px) {
        min-height: auto;
      }

      .icon_box_single_header {
        &_title {
          color: #fff;
        }
        svg {
          fill: #fff;
        }
      }

      .icon_box_single_body {
        color: #fff;
      }

      .icon_box_single_footer {
        @media (max-width: 767.98px) {
          bottom: auto;
          left: auto;
          position: relative;
        }
      }
    }

    svg {
      fill: #fff;
    }
  }
}

