  font-size: clamp(1rem, 4vw, 1.8rem);
  line-height: 1.28;
  font-weight: 300;

  strong {
    font-weight: 500;
  }
}

.about_section {
  @include smSpace;

  .section_intro {
    @media (max-width: 767.98px) {
      margin-bottom: 0;
    }
  }

  .para_md {
    @media (max-width: 767.98px) {
