    }
  }
}

.tree_bg {
  border-radius: 12px;
  height: 100%;
  inset: 0;
  opacity: 0.2;
  overflow: hidden;
  @extend %absolute;
  width: 100%;
  z-index: 0;

  img {
    height: 100%;
    width: 100%;
  }
}

.partners_section {
  @include smSpace;

  .section_intro {
    @media (max-width: 767.98px) {
      margin-bottom: 0;
    }
  }
}

.testimonials_section {
  @include smSpace;

  .swiper {
    height: 100%;
    width: 100%;

    @media (max-width: 767.98px) {
      padding: 0 20px;
    }
  }

  &.swiper_arrow_nav .swiper-button-next,
  &.swiper_arrow_nav .swiper-button-prev {
    @media (max-width: 767.98px) {
      bottom: 0;
      top: auto;
    }
  }

  &.swiper_arrow_nav .swiper-button-next {
    @media (max-width: 767.98px) {
      right: 38%;
    }
  }

  &.swiper_arrow_nav .swiper-button-prev {
    @media (max-width: 767.98px) {
      left: 38%;
    }
  }
}

