      width: 100%;
    }
    @media (min-width: 768px) and (max-width: 991.98px) {
      padding: 20px 20px 60px;
      width: 100%;
    }

    @media (max-width: 767.98px) {
      padding: 20px 20px 60px;
      width: 100%;
    }

    p {
      margin: 0;
    }
  }

  &_media {
    align-items: center;
    display: inline-flex;
    flex-wrap: wrap;
    gap: 10px;
    left: -3rem;
    @extend %relative;
    width: 24%;

    @media (min-width: 992px) and (max-width: 1199.98px) {
      flex-direction: column;
      left: auto;
      top: -3rem;
      width: 100%;
    }
    @media (min-width: 768px) and (max-width: 991.98px) {
      flex-direction: column;
      left: auto;
      top: -3rem;
      width: 100%;
    }

    @media (max-width: 767.98px) {
      flex-direction: column;
      left: auto;
