  }

  > a {
    @extend %absolute;
    inset: 0;
    height: 100%;
    width: 100%;
    z-index: 5;
  }

  &_media {
    aspect-ratio: 632/440;
    @extend .round_xl;
    overflow: hidden;

    img {
      height: 100%;
      transition: all 0.5s;
      width: 100%;
    }
  }

  &_content {
    padding: 30px 12px 0;

    &_title {
      @include font-size(20px);
      margin-bottom: 20px;
      /* 
      color: var(--primary_color);
      font-weight: 500;
      overflow: hidden;
      height: 52px;

      @media (min-width: 992px) and (max-width: 1199.98px) {
        height: auto;
      }

      @media (min-width: 768px) and (max-width: 991.98px) {
        height: auto;
      }

      @media (min-width: 576px) and (max-width: 767.98px) {
        height: auto;
        overflow: visible;
      }

      @media (max-width: 575.98px) {
        height: auto;
        overflow: visible;
      } 
      */
    }

    p {
      @include font-size(16px);
      line-height: 1.3;
      margin-bottom: 0;
    }

    a {
      align-items: center;
      color: var(--primary_color);
      display: inline-flex;
      font-weight: 500;
      gap: 4px;
      padding: 0;
      text-decoration: none;

      &:focus,
      &:hover {
        color: var(--secondary-color);
      }

      i {
        @include font-size(22px);
        line-height: 0;
      }
    }
  }

  &.info_shadow &_content {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 25px;

    @media (max-width: 767.98px) {
      padding: 12px;
    }

    &_title {
      @include font-size(24px);
      margin-bottom: 0;
    }

    p {
      color: var(--brand_color_3);
      overflow: hidden;
      line-height: 1.8;
      text-overflow: ellipsis;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      line-clamp: 3;
      display: -webkit-box;
