.solution_single {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 100%;
  justify-content: space-between;
  overflow: clip;
  padding-left: 32px;
  padding-right: 32px;
  padding-top: 64px;
  @extend %relative;

  &_title {
    @media (min-width: 1399.98px) {
      min-height: 80px;
      padding: 0 7%;
    }

    @media screen and (max-width: 1366px) {
      min-height: 92px;
    }

    @media (max-width: 767.98px) {
      min-height: auto;
    }
  }

  img {
    max-width: 100%;
  }

  &_backdrop {
    background-color: rgba(0, 0, 0);
    border-radius: 42px;
    display: block;
    inset: 0;
    @extend %absolute;
    top: 32px;
    transition: all 0.6s ease-in-out;
    width: 100%;
    z-index: -1;
  }

  &_content {
    display: grid;
    grid-template-rows: 1fr;
    min-height: 180px;

    p {
      opacity: 0.55;

      @media (min-width: 1024px) {
        @include font-size(14px);
        line-height: calc(20 / 14);
      }
    }
  }

  &_img {
    clip-path: inset(0 0 0 0);
    transition: all 0.6s ease-in-out;
  }

  @media (hover: hover) and (pointer: fine) {
    &:hover {
      .solution_single_backdrop {
        bottom: 32px;
        top: 0;
      }

      .solution_single_img {
        clip-path: inset(0 0 32px 0);
      }
    }
  }
}

