  text-align: center;
}

.e-nav_link {
  border: 1px solid var(--body_text_color);
  border-radius: 30px;
  color: var(--body_text_color);
  display: inline-block;
  font-size: 11px;
  height: 45px;
  letter-spacing: 0.1rem;
  line-height: 45px;
  margin: 0 15px;
  overflow: hidden;
  @extend %relative;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: top;
  width: 170px;

  &::before {
    background-color: var(--primary_color);
    content: "";
    height: 200px;
    position: absolute;
    right: 70px;
    top: 70px;
    transform: rotate(50deg);
    width: 260px;
  }

  &::after,
  &::before {
    transition: all 0.3s;
  }

  &::after {
    content: "Home Page";
    position: relative;
    z-index: 999;
  }

  @media (hover: hover) and (pointer: fine) {
    &:hover {
      border-color: var(--primary_color);
      &::before {
        right: -50px;
        top: -60px;
      }

      &::after {
        color: #fff;
      }
    }
  }
}

.e-nav_link:nth-child(2):hover:after {
  color: var(--body_text_color);
}

@media (max-width: 900px) {
  .nofound_message_title {
    font-size: 48px;
  }

  .nofound_content {
    top: 55%;
  }
}

@media (max-width: 750px) {
  .nofound_content {
    margin: 0 auto;
    padding-top: 300px;
    position: static;
    transform: none;
  }

  .nofound {
    height: 80vh;
    padding-bottom: 0;
    padding-top: 0;
  }
}

@media (max-width: 650px) {
  .nofound_message_title {
    font-size: 36px;
    line-height: 1;
    padding-bottom: 20px;
  }

  .nofound_message_text {
    font-size: 16px;
    line-height: 2;
    padding-left: 20px;
    padding-right: 20px;
  }

  .nofound_content {
    padding-top: 120px;
  }
}

@media (max-width: 480px) {
  .e-nav__form,
  .e-nav__link {
    height: 38px;
    line-height: 36px;
  }

  .nofound_content {
    padding-top: 0;
  }

  .nofound_message_title {
    font-size: 12vw;
  }

  .nofound_message_text {
    font-size: 15px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .nofound_nav {
    margin-top: 20px;
  }

  .e-nav_link {
    display: block;
    margin: 0 auto 15px;
  }
}

.sitemap_title {
  h2 {
    border-bottom: 1px solid var(--body_text_color);
    margin-bottom: 0;
    padding: 0 20px 20px;
  }
}

.sitemap_content {
  padding: 0 40px;

  @media (max-width: 767.98px) {
    padding: 0 20px;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;

    > li {
      flex: 0 1 33%;
      padding-right: 30px;

      @media (max-width: 767.98px) {
        flex: 1 1 100%;
      }

      a {
        color: var(--body_text_color);
        text-decoration: none;

        &:focus,
        &:hover {
          color: var(--primary_color);
        }
      }

      &:not(:last-child) {
        margin-bottom: 18px;
      }
    }
  }

  .sub-menu {
    margin-left: 20px;
    > li {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}
