.hover_effect {
  background: linear-gradient(45deg, #f58120 0%, #000 100%);
  height: 100%;
  overflow: hidden;
  width: 100%;
}
.hover_effect_overlay {
  height: 100%;
  left: 0;
  overflow: hidden;
  padding: 3rem;
  top: 0;
  width: 100%;
}
.hover_effect_overlay::before {
  border: 1px solid #fff;
  bottom: 20px;
  content: "";
  opacity: 0;
  left: 20px;
  right: 20px;
  top: 20px;
  transform: translate3d(-20px, 0, 0);
  transition: opacity 0.35s, transform 0.45s;
}
.hover_effect img {
  display: block;
  transform: translate3d(-40px, 0, 0);
  transition: opacity 0.35s, transform 0.45s;
  width: calc(100% + 60px);
}
@media (hover: hover) and (pointer: fine) {
  .hover_effect:hover img {
    opacity: 0.6;
    transform: translate3d(0, 0, 0);
  }
  .hover_effect:hover .hover_effect_overlay::before {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

