/* events */
.event_single {
  border-radius: 15px;
  height: 300px;
  margin-bottom: 30px;
  min-height: 320px;
  overflow: hidden;
  transition: all 0.6s;
}
.event_single_overlay {
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  height: 100%;
  inset: 0;
  justify-content: flex-end;
  overflow: hidden;
  padding: 25px;
  transition: all 0.6s;
  width: 100%;
}
.event_single_overlay_content {
  transition: all 0.9s ease;
}
.event_single_overlay_content_title {
  color: #fff;
}
.event_single_overlay_content p {
  color: #fff;
  margin: 0;
}
.event_single_overlay span {
  align-items: center;
  bottom: 10px;
  color: #fff;
  display: inline-flex;
  height: 40px;
  justify-content: center;
  opacity: 0;
  right: 20px;
  transition: all 0.5s ease;
  transition-delay: 0.3s;
  width: 40px;
  z-index: 0;
}
.event_single img {
  height: 100%;
  transition: all 0.5s ease;
  width: 100%;
}
.event_single.subs .event_single_overlay {
  align-items: center;
  background-color: rgb(var(--brand_color_3-rgb), 0.6);
  justify-content: center;
}
.event_single.subs .event_single_overlay_content_icon {
  background-color: #fff;
  border-radius: 50%;
  display: inline-flex;
  height: 120px;
  overflow: hidden;
  padding: 6px;
  width: 120px;
}
.event_single.subs .event_single_overlay_content_icon img {
  object-fit: contain;
  height: 100%;
  width: 100%;
}
@media (hover: hover) and (pointer: fine) {
  .event_single:hover .event_single_overlay {
    background-color: rgb(var(--brand_color_3-rgb), 0.8);
  }
  .event_single:hover .event_single_overlay_content {
    transform: translateY(-30px);
  }
  .event_single:hover span {
    transform: translateX(10px);
    opacity: 1;
  }
  .event_single:hover img {
    transform: scale(1.1);
  }
}

