.nofound {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 70vh;
  overflow: hidden;
  text-align: center;
  width: 100%;
}
.nofound_overlay {
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
}
.nofound_content {
  width: 50%;
}
@media (min-width: 576px) and (max-width: 767.98px) {
  .nofound_content {
    width: 80%;
  }
}
@media (max-width: 575.98px) {
  .nofound_content {
    width: 90%;
  }
}
.nofound_message {
  text-align: center;
}
.nofound_message_title {
  font-size: 5.6rem;
  font-weight: 600;
  letter-spacing: 0.2em;
  line-height: 90px;
  padding-bottom: 40px;
  text-transform: uppercase;
}
.nofound_message_text {
  font-size: 18px;
  line-height: 42px;
  margin: auto;
  padding: 0 60px;
}

.nofound_nav {
  margin: 40px auto 0;
  max-width: 600px;
  text-align: center;
}

.e-nav_link {
  border: 1px solid var(--body_text_color);
  border-radius: 30px;
  color: var(--body_text_color);
  display: inline-block;
  font-size: 11px;
  height: 45px;
  letter-spacing: 0.1rem;
  line-height: 45px;
  margin: 0 15px;
  overflow: hidden;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: top;
  width: 170px;
}
.e-nav_link::before {
  background-color: var(--primary_color);
  content: "";
  height: 200px;
  position: absolute;
  right: 70px;
  top: 70px;
  transform: rotate(50deg);
  width: 260px;
}
.e-nav_link::after, .e-nav_link::before {
  transition: all 0.3s;
}
.e-nav_link::after {
  content: "Home Page";
  position: relative;
  z-index: 999;
}
@media (hover: hover) and (pointer: fine) {
  .e-nav_link:hover {
    border-color: var(--primary_color);
  }
  .e-nav_link:hover::before {
    right: -50px;
    top: -60px;
  }
  .e-nav_link:hover::after {
    color: #fff;
  }
}

.e-nav_link:nth-child(2):hover:after {
  color: var(--body_text_color);
}

@media (max-width: 900px) {
  .nofound_message_title {
    font-size: 48px;
  }
  .nofound_content {
    top: 55%;
  }
}
@media (max-width: 750px) {
  .nofound_content {
    margin: 0 auto;
    padding-top: 300px;
    position: static;
    transform: none;
  }
  .nofound {
    height: 80vh;
    padding-bottom: 0;
    padding-top: 0;
  }
}
@media (max-width: 650px) {
  .nofound_message_title {
    font-size: 36px;
