@use "../mixable" as *;
@use "../global" as *;

/* footer start */
.footer_top {
  clear: both;
  @extend %relative;
  width: 100%;

  img {
    width: 100%;

    @media (max-width: 767.98px) {
      height: 25vw;
    }
  }
}

footer {
  clear: both;
  color: #b3b3b3;
  display: block;
  font-weight: 300;
  padding-top: 60px;
  width: 100%;

  .content_wrapper {
    border-radius: 20px 20px 0 0;
  }

  .col_divider {
    [class^="col-"]:not(:last-child) {
      border-right: 1px solid #635c70;
      padding-bottom: 2.5rem;

      @media screen and (min-width: 1921px) {
        padding-bottom: 5.5rem;
      }

      @media (min-width: 768px) and (max-width: 991.98px) {
        border-right: 0 none;
      }

      @media (max-width: 767.98px) {
        border-bottom: 1px solid #635c70;
        border-right: 0 none;
        margin-bottom: 10px;
        padding-bottom: 0;
      }
    }

    [class^="col-"]:not(:first-child) {
      padding-left: 2.5rem;

      @media screen and (min-width: 1921px) {
        padding-left: 5rem;
      }

      @media (max-width: 767.98px) {
        padding-left: 0;
      }
    }

    [class^="col-"]:first-child {
      @media (min-width: 768px) and (max-width: 991.98px) {
        padding-left: 2.5rem;
      }
    }

    [class^="col-"]:nth-child(odd) {
      @media (min-width: 768px) and (max-width: 991.98px) {
        border-right: 1px solid #635c70;
      }
    }
  }

  ul {
    font-size: 0;
    @include list_clear;
  }

  a {
    color: #b3b3b3;
    display: inline-block;
    text-decoration: none;

    &:focus,
    &:hover {
      opacity: 0.8;
    }
  }

  .footer_logo {
    display: inline-block;
    @extend %relative;
    width: 210px;

    @media (min-width: 992px) and (max-width: 1199.98px) {
      margin-bottom: 30px;
    }

    @media (min-width: 768px) and (max-width: 991.98px) {
      margin-bottom: 30px;
    }

    @media (min-width: 576px) and (max-width: 767.98px) {
      margin-bottom: 30px;
    }

    @media (max-width: 575.98px) {
      margin-bottom: 30px;
    }

    img {
      width: 100%;
    }
  }

  .footer_single {
    @include font-size(14px);

    h5 {
      color: #fff;
      @include font-size(18px);
      font-weight: 300;
      margin-bottom: 0;
      @extend %relative;
      text-transform: uppercase;
      width: 100%;
    }

    p {
      margin-top: 40px;
      opacity: 0.65;

      @media (max-width: 767.98px) {
        margin-top: 18px;
      }
    }

    ul {
      @include font-size(14px);
      margin-top: 22px;
      // opacity: 0.65;

      @media (max-width: 767.98px) {
        margin-bottom: 20px;
        margin-top: 18px;
      }

      li {
        margin-bottom: 10px;

        a {
          font-weight: 300;
          &:focus,
          &:hover {
            transform: translateX(10px);
          }
        }
      }
    }
  }

  .extended_footer {
    @extend %relative;
    width: 100%;
    @media (max-width: 767.98px) {
      padding: 0 20px;
    }
  }

  .social_footer {
    ul {
      > li {
        display: inline-block;

        a {
          color: #fff;
          @include font-size(18px);

          &:focus,
          &:hover {
            color: var(--gray_c);
          }

          img {
            width: 100%;
          }
        }
      }
    }
  }

  .copyright {
    @include font-size(14px);
    opacity: 0.65;
  }

  .other_links {
    text-transform: uppercase;
    // opacity: 0.65;

    ul {
      @include font-size(14px);
    }

    li {
      display: inline-flex;
      padding: 0 10px;

      @media (min-width: 576px) and (max-width: 767.98px) {
        margin-bottom: 5px;
      }

      @media (max-width: 575.98px) {
        margin-bottom: 5px;
      }
    }
  }
}

/* footer end */
