@use "../mixable" as *;
@use "../global" as *;

/* hero start */
.home_hero_slider {
  font-size: 0;
  @extend %relative;
  width: 100%;

  figure {
    margin: 0;
    @extend %relative;

    figcaption {
      left: 48%;
      @extend %absolute;
      top: 20%;
      text-transform: uppercase;

      h4 {
        @include font-size(70px);
        line-height: 65px;

        span {
          color: var(--primary_color);
          display: block;
        }
      }
    }
  }

  .owl-nav {
    @extend %absolute;
    top: 46%;
    transform: translateY(-50%);
    width: 100%;
    z-index: 3;

    button[type="button"] {
      background-color: #fff;
      border-radius: 50%;
      height: 40px;
      line-height: 58px;
      @extend %absolute;
      transition: all 0.3s ease-in-out 0s;
      width: 40px;

      &:focus,
      &:hover {
        background-color: var(--primary_color);
      }

      span {
        display: inline-block;
        @include font-size(20px);
      }
    }

    button.owl-next {
      right: 20px;

      span {
        margin-left: 3px;
      }
    }

    button.owl-prev {
      left: 20px;

      span {
        margin-right: 3px;
      }
    }
  }

  .owl-dots {
    bottom: 80px;
    position: absolute;
    width: 100%;

    @media (max-width: 575.98px) {
      bottom: 40px;
    }

    .owl-dot {
      span {
        background: transparent;
        border: 1px solid #fff;
      }

      &.active {
        span {
          background: #fff;
        }
      }
    }
  }
}

.hero_img_wrapper {
  @extend %relative;
  width: 100%;

  img {
    display: inline-block;
    width: 100%;
  }
}

.hero_text_wrapper {
  align-items: center;
  display: flex;
  height: 100%;
  inset: 0;
  justify-content: center;
  @extend %absolute;
  width: 100%;
  z-index: 2;

  &_content {
    display: inline-block;
    // width: 90%;

    &_title {
      display: flex;
      font-weight: 500;

      @media (max-width: 767.98px) {
        flex-direction: column;
      }

      .page_name {
        color: #fff;
        display: inline-flex;
        font-size: 2vw;
        line-height: 1;
        @extend %relative;
        text-transform: uppercase;
        white-space: nowrap;

        @media screen and (min-width: 1499px) {
          font-size: 2.6vw;
        }

        @media screen and (min-width: 1600px) {
          font-size: 2.6vw;
        }

        @media screen and (min-width: 1921px) {
          font-size: 2vw;
        }
        @media screen and (max-width: 1440px) {
          font-size: 3vw;
        }
        @media screen and (max-width: 1366px) {
          font-size: 3vw;
        }

        @media (max-width: 767.98px) {
          align-items: center;
          font-size: 5vw;
          flex-direction: column;
        }

        &::after {
          background-color: var(--brand_color_300);
          content: "";
          display: inline-block;
          height: 3px;
          margin: 0 10px;
          @extend %relative;
          top: 1vw;
          width: 2vw;

          @media (max-width: 767.98px) {
            margin: 12px 0;
            width: 16vw;
          }
        }
      }

      .page_tagline {
        @include list_clear;

        li {
          color: #fff;
          display: block;
          font-size: 3.2vw;
          font-style: italic;
          letter-spacing: -3px;

          @media screen and (min-width: 1499px) {
            font-size: 4.5vw;
          }

          @media screen and (min-width: 1600px) {
            font-size: 4.5vw;
          }

          @media screen and (min-width: 1921px) {
            font-size: 3.2vw;
          }

          @media screen and (max-width: 1440px) {
            font-size: 4.6vw;
          }

          @media screen and (max-width: 1366px) {
            font-size: 4.6vw;
          }

          @media (min-width: 992px) and (max-width: 1199.98px) {
            line-height: 1;
          }

          @media (min-width: 768px) and (max-width: 991.98px) {
            line-height: 1;
          }

          @media (max-width: 767.98px) {
            font-size: 5.6vw;
            letter-spacing: 0;
          }

          &:last-child {
            padding-left: 6vw;
          }

          span {
            color: var(--brand_color_300);
            display: inline-block;
            font-style: normal;
            line-height: 1;
          }
        }
      }
    }
  }
}

.no-tagline {
  .hero_text_wrapper {
    &_content {
      &_title {
        .page_name {
          &::after {
            display: none;
          }
        }
      }
    }
  }
}

.hero_wrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  @extend %relative;
}

.home_hero_top {
  @extend %relative;
  width: 85%;

  .hero_text {
    @extend %relative;
    z-index: 2;
  }

  h1 {
    font-size: 80px;
    margin-bottom: 0;
    @extend %relative;

    @media (max-width: 767.98px) {
      font-size: 36px;
    }
  }

  p {
    @include font-size(18px);
    padding: 2rem 0;
  }

  .btn_fill {
    margin: 2rem 0;
  }
}

.hero_grid_img {
  @extend %absolute;
  top: -2rem;
  text-align: center;
  width: 100%;
  z-index: 0;

  img {
    display: inline-block;
    width: 100%;
  }
}

.highlight_grid_img {
  left: 0;
  margin-top: -82px;
  @extend %absolute;
  width: 100%;
  z-index: 3;

  ul {
    display: inline-flex;
    font-size: 0;
    gap: 2rem;
    @include list_clear;

    li {
      @extend %relative;

      &:first-child {
        img {
          margin-top: 1rem;
          width: 8rem;
        }
      }

      &:nth-child(2) {
        img {
          width: 274px;
        }
      }

      &:last-child {
        img {
          margin-top: 4rem;
          width: 10rem;
        }
      }
    }
  }
}

.home_hero_bottom {
  margin-top: 10rem;
  @media (max-width: 767.98px) {
    margin-top: 0;
  }
}

.arrow_top {
  left: 51%;
  margin-top: -200px;
  @extend %absolute;
  width: 256px;
  z-index: 4;

  img {
    width: 100%;
  }
}

.bottom_cube {
  img {
    width: 100%;
  }
}

.hero_item {
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @extend %relative;
  overflow: hidden;
  width: 100%;

  @media (min-width: 992px) and (max-width: 1199.98px) {
    height: 650px;
  }

  video,
  img {
    display: inline-block;
    width: 100%;
  }
}

.product_hero_row {
  display: flex;
  padding: 80px 0 40px;
  justify-content: center;

  @media (min-width: 576px) and (max-width: 767.98px) {
    flex-direction: column-reverse;
    flex-wrap: wrap;
  }

  @media (max-width: 575.98px) {
    flex-direction: column-reverse;
    flex-wrap: wrap;
  }

  .color_btn_holder,
  .product_snap,
  .product_desc {
    color: #fff;
    display: flex;
  }

  .color_btn_holder,
  .product_desc {
    bottom: 30px;
    @extend %absolute;
  }

  .color_btn_holder {
    left: 30px;

    @media (min-width: 576px) and (max-width: 767.98px) {
      justify-content: center;
      left: 0;
      position: relative;
      top: 20px;
      width: 100%;
    }

    @media (max-width: 575.98px) {
      justify-content: center;
      left: 0;
      position: relative;
      top: 20px;
      width: 100%;
    }

    .btn_fill {
      background-color: var(--accent_color);
      border-color: transparent;
      color: var(--highlight_color);
      @include font-size(14px);
      padding: 12px 0;
      text-transform: none;
      width: 200px;

      &:focus,
      &:hover {
        color: #fff;
      }
    }
  }

  .product_snap {
    @media (min-width: 576px) and (max-width: 767.98px) {
      margin: auto;
      width: 50%;
    }

    @media (max-width: 575.98px) {
      margin: auto;
      width: 50%;
    }
  }

  .product_desc {
    color: #fff;
    flex-direction: column;
    right: 30px;
    text-align: right;
    width: 350px;

    @media (min-width: 576px) and (max-width: 767.98px) {
      position: relative;
      text-align: center;
      width: 100%;
      top: 0;
      padding: 20px;
      right: 0;
    }

    @media (max-width: 575.98px) {
      position: relative;
      text-align: center;
      width: 100%;
      top: 0;
      padding: 20px;
      right: 0;
    }

    h2,
    p,
    a {
      color: #fff;
    }

    p,
    a {
      @include font-size(16px);
    }

    h2,
    p {
      margin-bottom: 30px;
    }

    .btn_fill {
      background-color: var(--highlight_color);
      border-color: transparent;
      color: #fff;
      padding: 16px 0;

      @media (min-width: 576px) and (max-width: 767.98px) {
        width: 180px;
        margin: auto;
        padding: 10px 0;
      }

      @media (max-width: 575.98px) {
        width: 180px;
        margin: auto;
        padding: 10px 0;
      }
    }
  }
}

.pic_text_holder {
  left: 15vw;
  @extend %absolute;
  // @include transform(translateX(-50%));
  text-align: left;
  top: 26%;
  width: 40%;
  z-index: 2;

  @media screen and (max-width: 1440px) {
    right: 8vw;
    top: 8vh;
    width: 60%;
  }

  @media screen and (max-width: 1366px) {
    right: 8vw;
    top: 8vh;
    width: 60%;
  }

  @media (min-width: 992px) and (max-width: 1199.98px) {
    right: 2vw;
    top: 10vh;
  }

  @media (min-width: 768px) and (max-width: 991.98px) {
    right: 0;
    top: 5vh;
    width: 310px;
  }

  @media (min-width: 576px) and (max-width: 767.98px) {
    right: -30px;
    top: 10vh;
    width: 310px;
  }

  @media (max-width: 575.98px) {
    height: 45%;
    left: 20px;
    right: auto;
    top: 8vh;
    width: 80%;
  }

  h2 {
    @include font-size(80px);
    font-weight: normal;
    letter-spacing: -2px;
    line-height: 70px;
    margin: 0;
    text-transform: uppercase;

    @media screen and (max-width: 1440px) {
      @include font-size(70px);
      line-height: 60px;
    }

    @media screen and (max-width: 1366px) {
      @include font-size(70px);
      line-height: 60px;
    }

    @media (min-width: 768px) and (max-width: 991.98px) {
      @include font-size(55px);
      line-height: 50px;
      letter-spacing: 0;
    }

    @media (min-width: 576px) and (max-width: 767.98px) {
      @include font-size(27px);
      letter-spacing: 0;
    }

    @media (max-width: 575.98px) {
      @include font-size(30px);
      letter-spacing: -1px;
      line-height: 30px;
    }

    span {
      display: block;
    }
  }

  p {
    color: #fff;
    @include font-size(16px);
    text-transform: uppercase;

    @media (max-width: 575.98px) {
      display: none;
    }
  }

  .btn_outline {
    @include font-size(14px);
    padding: 5px 15px;

    @media (max-width: 575.98px) {
      bottom: 20px;
      @include font-size(14px);
      padding: 6px 20px + 2;
      position: absolute;
    }
  }
}

.hero_holder {
  font-size: 0;
  position: relative;
  width: 100%;
  z-index: 1;

  figure {
    margin: 0;
  }

  .hero_slider {
    float: left;
    width: 100%;
  }

  .owl-nav {
    @extend %absolute;
    top: 30%;
    transform: translateY(-30%);
    width: 100%;
    z-index: 3;

    [class*="owl-"] {
      &:focus,
      &:hover {
        background: transparent;
      }
    }

    button.owl-next,
    button.owl-prev {
      position: absolute;

      span {
        color: #fff;
        @include font-size(50px);

        @media (min-width: 768px) and (max-width: 991.98px) {
          @include font-size(40px);
        }

        @media (min-width: 576px) and (max-width: 767.98px) {
          @include font-size(36px);
        }

        @media (max-width: 575.98px) {
          @include font-size(26px);
        }
      }
    }

    button.owl-prev {
      left: 20px;
    }

    button.owl-next {
      right: 20px;
    }
  }

  .inner_hero {
    @extend %relative;
    width: 100%;

    img {
      @extend %relative;
      width: 100%;
    }

    h2 {
      bottom: 90px;
      color: #fff;
      display: inline-block;
      @include font-size(80px);
      left: 300px;
      letter-spacing: -3px;
      line-height: 70px;
      @extend %absolute;
      text-transform: uppercase;
      width: 200px;
      z-index: 2;
    }

    .jumbotron {
      background-color: transparent;
      bottom: 10px;
      padding: 0;
      position: absolute;
      width: 100%;
      z-index: 1;

      @media (max-width: 575.98px) {
        bottom: 15px;
        margin-bottom: 0;
      }

      h1 {
        color: var(--primary_color);
        @include font-size(130px);

        @media (min-width: 768px) and (max-width: 991.98px) {
          @include font-size(70px);
        }

        @media (min-width: 576px) and (max-width: 767.98px) {
          @include font-size(50px);
        }

        @media (max-width: 575.98px) {
          @include font-size(46px);
        }
      }
    }
  }
}

/* hero end */
